@import "https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css";
@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css";

/*-------Home----------*/

.home{
    background-image: url(./images/home-logo.jpg);
    width: 100%;
    height: 100%;
    background-position: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 10rem;
}

.bg-img {
    width:280px;
    min-height: 350px;
}

.caption{
    width: auto;
    background-color: aqua;
    text-align: center;
    text-transform: uppercase;
}


/*-------My Work----------*/

.mywork{
    background-image: url(./images/work-img.jpeg);
    width: 100%;
    height: 100%;
    background-position: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
}

.card{
    margin: auto;
}

.card-btn{
  margin: 10px;
}


/*-----------Resources-----------*/

.resources{
    background-image: url(./images/resources-img.jpeg);
    width: 100%;
    height: 100%;
    background-position: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
}

.resources .card-img-top{
    height: 300px;
}


/*-----------Contacts-----------*/

.contacts{
    background: linear-gradient(35deg,#00ffff,#eeaeca,#00fff4);
}

.fa {
   width: 70px;
   height: 70px;
   text-align: center;
   text-decoration: none;
   font-size: 30px;
   line-height: 40px;
   border-radius: 50%;
   padding: 15px;
   margin: 20px;
   transition: all 300ms ease-in-out;
  }
  
 
  .fa-facebook {
    background: #3B5998;
    color: #fff;
  }
  .fa-facebook:hover {
    background: #fff;
    color: #3B5998;
    border:1px solid #3B5998;
  }

  .fa-github {
     background: #000;
     color: #fff;
  }
  .fa-github:hover {
    background: #fff;
    color: #000;
    border:1px solid #000;
  }

  .fa-linkedin {
    background: #007bb5;
    color: white;
  }
  .fa-linkedin:hover {
    background: #fff;
    color: #007bb5;
    border:1px solid #007bb5;
  }

  .fa-envelope {
    background: #dd4b39;
    color: white;
  }
  .fa-envelope:hover {
    background: #fff;
    color: #dd4b39;
    border:1px solid #dd4b39;
  }

  .fa-snapchat-ghost {
    background: #fffc00;
    color: white;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  }
  .fa-snapchat-ghost:hover {
    background: #fff;
    color: #fffc00;
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
    border:1px solid #fffc00;
  }


  @media all and (max-width:720px){
      .fa{
        width: 40px;
        height: 40px;
        text-align: center;
        text-decoration: none;
        font-size: 10px;
        line-height: 10px;
        border-radius: 50%;
        padding: 15px;
        transition: all 300ms ease-in-out;
       margin: 10px;
      }
  }